.address-list {
  padding-top: 0.266667rem;
}

h3 {
  color: #333;
}

.address-box .ub-bd .ub-btn {
  float: right;
}

.address-box .ub-bd:first-child {
  background-image: -webkit-linear-gradient(-45deg, #fa393e 12.5%, #fff 12.5%, #fff 37.5%, #2490c8 37.5%, #2490c8 62.5%, #fff 62.5%, #fff 87.5%, #fa393c 87.5%);
  background-size: 1.333333rem 0.066667rem;
  background-repeat: repeat-x;
}

.address-box .ub-btn a {
  display: inline-block;
  width: 4em;
  padding: 0.066667rem 0;
  text-align: center;
  background: #f0eff5;
}

.address-box .ub-btn a img {
  vertical-align: -4PX;
}


