.time-banner {
  position: relative;
  float: left;
}

.time-banner .time-clock {
  display: inline-block;
}

.time-banner .day,
.time-banner .hour,
.time-banner .min,
.time-banner .sec {
  padding: 0 0.04rem;
}

.time-banner .over {
  color: #ff0000;
}


